.Register {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 65vh;
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
}

.parent {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 768px) {
  .register_area {
    padding-left: 1rem;
    padding-right: 1rem;
  }
}
