.main_projects_container {
  padding: 2rem 0;
  margin: 4rem 0;
}

.projects {
  display: grid;
  grid-gap: 2rem;
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

.project {
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
}
.img_container {
  width: 100%;
}
.img_container img {
  max-width: 100%;
}

.project .details {
  display: flex;
  flex-direction: column;
}

.project a,
.project h3 {
  margin: 0.5rem 0;
  text-align: center;
  display: block;
}
.project h3 {
  font-size: 1rem;
}

@media (max-width: 768px) {
  .projects {
    grid-template-columns: repeat(1, minmax(0, 1fr));
    grid-gap: 4rem;
  }
}
